import React, { useEffect, useState, Fragment } from "react";
import { auth } from "firebaseUtil/firebase";
import { carlos } from "firebaseUtil/user";
import cookieOptions from "Util/cookieUtil";
import { reduxDispatch } from "redux/store";
import * as types from "redux/module/loginModule/action";
import { withCookies } from "react-cookie";
import { doSignInWithEmailAndPassword } from "firebaseUtil/auth";

//開發模式下，自動登入
const DevelopmentLogin = ({ history, cookies }) => {

    const [enableAlert, setEnableAlert] = useState(false);
    const [alertMessage, setAlertMessage] = useState("");
    const [username, setUsername] = useState("");

    useEffect(() => {
        setUsername('k');
    }, [])

    useEffect(() => {

        if (process.env.NODE_ENV === "development" && username !== "") {
            const email = "k@klk.coffee";
            const password = "123456!QAZxsw2";

            if (auth) {
                doSignInWithEmailAndPassword(auth, email, password)
                    .then(() => {
                        // // firebase 登入成功
                        reduxDispatch(types.SET_LOGIN_USER, username);

                        if (username === carlos) {
                            carlosLogin();
                        }
                    })
                    .catch(error => {
                        if (error.message) {
                            setEnableAlert(true);
                            setAlertMessage(error.message);
                        }
                    });
            } else {
                console.log("firebase error, auth not exist");

                setEnableAlert(true);
                setAlertMessage("System error, please notify the administrator");
            }
        }
    }, [username])

    //若為指定使用者, 則寫入使用者資訊到 cookie
    const carlosLogin = () => {
        if (cookies) {
            cookies.set("loginUser", carlos, cookieOptions);
        }
    }

    return (
        <Fragment />
    )
}

export default withCookies(DevelopmentLogin);