import React, { useState, Fragment } from "react";
import loadable from "@loadable/component";
import { useSelector } from "react-redux";
import { carlos, linda } from "firebaseUtil/user";
import VerifyLogin from "pages/Verify/VerifyLogin";
import DevelopmentLogin from "components/Development/DevelopmentLogin";

const ChatPage = () => {

    const [sesameOpen, setSesameOpen] = useState(false);
    const { user } = useSelector(state => state.login);


    const RenderComponent = () => {
        
        if (sesameOpen === false) {
            const ChatDefaultPage = loadable(() => import("./ChatDefaultPage"));
            return (
                <ChatDefaultPage setSesameOpen={setSesameOpen} />
            )
        } else {
            if (user === carlos) {
                const AdminChatPage = loadable(() => import("./AdminChatPage"));
                return (
                    <AdminChatPage />
                )
            } else if (user === linda) {
                const UserChatPage = loadable(() => import("./UserChatPage"));
                return (
                    <UserChatPage />
                )
            } else {
                return (<></>)
            }
        }
    }

    return (
        <Fragment>
            <RenderComponent />
            <VerifyLogin />
            {/* <DevelopmentLogin /> */}
        </Fragment>
    )
}

export default ChatPage;