import { carlos } from "firebaseUtil/user";
import { useSelector } from "react-redux";
import { reduxDispatch } from "redux/store";
import * as loginTypes from "redux/module/loginModule/action";
import * as chatTypes from "redux/module/chatModule/action"

const useStyles = makeStyles(styles);

export default function HeaderLinks(props) {

  const classes = useStyles();
  const { user } = useSelector(state => state.login);
  const UserDropdownList = [];
  const AdminDropdownList = [
    <Link to={SetAppVersionUrl} className={classes.dropdownLink}>
      <GTranslateIcon className={classes.dropdownIcons} /> Version Update
    </Link>,
  ];

  const easeInOutQuad = (t, b, c, d) => {
    t /= d / 2;
    if (t < 1) return (c / 2) * t * t + b;
    t--;
    return (-c / 2) * (t * (t - 2) - 1) + b;
  };

  const smoothScroll = (e, target) => {
    if (window.location.pathname === "/sections") {
      var isMobile = navigator.userAgent.match(
        /(iPad)|(iPhone)|(iPod)|(android)|(webOS)/i
      );
      if (isMobile) {
        // if we are on mobile device the scroll into view will be managed by the browser
      } else {
        e.preventDefault();
        var targetScroll = document.getElementById(target);
        scrollGo(document.documentElement, targetScroll.offsetTop, 1250);
      }
    }
  };
  const scrollGo = (element, to, duration) => {
    var start = element.scrollTop,
      change = to - start,
      currentTime = 0,
      increment = 20;

    var animateScroll = function () {
      currentTime += increment;
      var val = easeInOutQuad(currentTime, start, change, duration);
      element.scrollTop = val;
      if (currentTime < duration) {
        setTimeout(animateScroll, increment);
      }
    };
    animateScroll();
  };
  var onClickSections = {};

  const { dropdownHoverColor } = props;


  const onFonSizeChange = (e, size) => {
    e.preventDefault();
    if (size) {
      reduxDispatch(loginTypes.SET_MESSAGE_FONT_SIZE, size);
    }
  }

  const onMessageMaxNumberChange = (e, number) => {
    e.preventDefault();
    if (number) {
      reduxDispatch(chatTypes.SET_MAX_MESSAGE_NUMBER, number);
    }
  }

  return (
    <List className={classes.list + " " + classes.mlAuto}>
      <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          navDropdown
          hoverColor={dropdownHoverColor}
          buttonText="訊息內容數量"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={ChatIcon}
          dropdownList={[
            <Link to="/ch" onClick={(e) => { onMessageMaxNumberChange(e, 70) }} className={classes.dropdownLink}>
              <SmsIcon className={classes.dropdownIcons} /> 預設
            </Link>,
              <Link to="/ch" onClick={(e) => { onMessageMaxNumberChange(e, 200) }} className={classes.dropdownLink}>
                <SmsIcon className={classes.dropdownIcons} /> 200 則
            </Link>,
              <Link to="/ch" onClick={(e) => { onMessageMaxNumberChange(e, 500) }} className={classes.dropdownLink}>
                <SmsIcon className={classes.dropdownIcons} /> 500 則
            </Link>,
              <Link to="/ch" onClick={(e) => { onMessageMaxNumberChange(e, 1000) }} className={classes.dropdownLink}>
                <SmsIcon className={classes.dropdownIcons} /> 1000 則
            </Link>,
          ]}
        />
      </ListItem>
      <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          navDropdown
          hoverColor={dropdownHoverColor}
          buttonText="字體大小"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={TitleIcon}
          dropdownList={[
            <Link to="/ch" onClick={(e) => { onFonSizeChange(e, "0.7rem") }} className={classes.dropdownLink}>
              <TextFields className={classes.dropdownIcons} /> 小
          </Link>,
            <Link to="/ch" onClick={(e) => { onFonSizeChange(e, "1.0rem") }} className={classes.dropdownLink}>
              <TextFields className={classes.dropdownIcons} /> 中
          </Link>,
            <Link to="/ch" onClick={(e) => { onFonSizeChange(e, "1.3rem") }} className={classes.dropdownLink}>
              <TextFields className={classes.dropdownIcons} /> 大
          </Link>,
            <Link to="/ch" onClick={(e) => { onFonSizeChange(e, "1.6rem") }} className={classes.dropdownLink}>
              <TextFields className={classes.dropdownIcons} /> 超大
          </Link>,
          ]}
        />
      </ListItem>
      <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          navDropdown
          hoverColor={dropdownHoverColor}
          buttonText="Language"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={LanguageIcon}
          dropdownList={
            user && user === carlos ? AdminDropdownList : UserDropdownList
          }
        // dropdownList={[
        //   <Link to="/about-us" className={classes.dropdownLink}>
        //     <AccountBalance className={classes.dropdownIcons} /> About Us
        //   </Link>,
        //   <Link to="/blog-post" className={classes.dropdownLink}>
        //     <ArtTrack className={classes.dropdownIcons} /> Blog Post
        //   </Link>,
        //   <Link to="/blog-posts" className={classes.dropdownLink}>
        //     <ViewQuilt className={classes.dropdownIcons} /> Blog Posts
        //   </Link>,
        //   <Link to="/contact-us" className={classes.dropdownLink}>
        //     <LocationOn className={classes.dropdownIcons} /> Contact Us
        //   </Link>,
        //   <Link to="/landing-page" className={classes.dropdownLink}>
        //     <ViewDay className={classes.dropdownIcons} /> Landing Page
        //   </Link>,
        //   <Link to="/login-page" className={classes.dropdownLink}>
        //     <Fingerprint className={classes.dropdownIcons} /> Login Page
        //   </Link>,
        //   <Link to="/pricing" className={classes.dropdownLink}>
        //     <AttachMoney className={classes.dropdownIcons} /> Pricing Page
        //   </Link>,
        //   <Link to="/shopping-cart-page" className={classes.dropdownLink}>
        //     <ShoppingBasket className={classes.dropdownIcons} /> Shopping Cart
        //   </Link>,
        //   <Link to="/ecommerce-page" className={classes.dropdownLink}>
        //     <Store className={classes.dropdownIcons} /> Ecommerce Page
        //   </Link>,
        //   <Link to="/product-page" className={classes.dropdownLink}>
        //     <ShoppingCart className={classes.dropdownIcons} /> Product Page
        //   </Link>,
        //   <Link to="/profile-page" className={classes.dropdownLink}>
        //     <AccountCircle className={classes.dropdownIcons} /> Profile Page
        //   </Link>,
        //   <Link to="/signup-page" className={classes.dropdownLink}>
        //     <PersonAdd className={classes.dropdownIcons} /> Signup Page
        //   </Link>,
        //   <Link to="/error-page" className={classes.dropdownLink}>
        //     <Error className={classes.dropdownIcons} /> Error Page
        //   </Link>
        // ]}
        />
      </ListItem>
      {/* <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          navDropdown
          hoverColor={dropdownHoverColor}
          buttonText="Components"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={Apps}
          dropdownList={[
            <Link to="/" className={classes.dropdownLink}>
              <LineStyle className={classes.dropdownIcons} /> Presentation Page
            </Link>,
            <Link to="/components" className={classes.dropdownLink}>
              <Layers className={classes.dropdownIcons} />
              All components
            </Link>,
            <a
              href="https://demos.creative-tim.com/material-kit-pro-react/#/documentation/tutorial?ref=mkpr-navbar"
              target="_blank"
              className={classes.dropdownLink}
            >
              <Icon className={classes.dropdownIcons}>content_paste</Icon>
              Documentation
            </a>
          ]}
        />
      </ListItem>
      <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          navDropdown
          hoverColor={dropdownHoverColor}
          buttonText="Sections"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          buttonIcon={ViewDay}
          dropdownList={[
            <Link
              to="/sections#headers"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "headers")}
            >
              <Dns className={classes.dropdownIcons} /> Headers
            </Link>,
            <Link
              to="/sections#features"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "features")}
            >
              <Build className={classes.dropdownIcons} /> Features
            </Link>,
            <Link
              to="/sections#blogs"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "blogs")}
            >
              <ListIcon className={classes.dropdownIcons} /> Blogs
            </Link>,
            <Link
              to="/sections#teams"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "teams")}
            >
              <People className={classes.dropdownIcons} /> Teams
            </Link>,
            <Link
              to="/sections#projects"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "projects")}
            >
              <Assignment className={classes.dropdownIcons} /> Projects
            </Link>,
            <Link
              to="/sections#pricing"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "pricing")}
            >
              <MonetizationOn className={classes.dropdownIcons} /> Pricing
            </Link>,
            <Link
              to="/sections#testimonials"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "testimonials")}
            >
              <Chat className={classes.dropdownIcons} /> Testimonials
            </Link>,
            <Link
              to="/sections#contacts"
              className={classes.dropdownLink}
              onClick={e => smoothScroll(e, "contacts")}
            >
              <Call className={classes.dropdownIcons} /> Contacts
            </Link>
          ]}
        />
      </ListItem> */}

      {/* <ListItem className={classes.listItem}>
        <Button
          href="https://www.creative-tim.com/product/material-kit-pro-react?ref=mkpr-navbar"
          color={window.innerWidth < 960 ? "info" : "white"}
          target="_blank"
          className={classes.navButton}
          round
        >
          <ShoppingCart className={classes.icons} /> buy now
        </Button>
      </ListItem> */}
    </List>
  );
}

HeaderLinks.defaultProps = {
  hoverColor: "primary"
};

HeaderLinks.propTypes = {
  dropdownHoverColor: PropTypes.oneOf([
    "dark",
    "primary",
    "info",
    "success",
    "warning",
    "danger",
    "rose"
  ])
};
