import _ from "lodash";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
import { reduxDispatch } from "redux/store";
import * as loginTypes from "redux/module/loginModule/action";
import * as paths from "routes/constUrl";
import { appVersionRef } from "firebaseUtil/firebase";
import productionVersion from "deployment/appversion";
import { linda } from "firebaseUtil/user";
import { sendTelegramMessage } from "Util/TelegramUtil";
import { onAuthStateChanged, signOut } from "firebase/auth";
import { limitToLast, onValue, query, ref as databaseRef } from "firebase/database";
import { auth } from "firebaseUtil/firebase";
import { db } from "firebaseUtil/firebase";
import { ToastContainer, toast, Flip } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import '../../assets/css/react-toastfy-custom.css';

const VerifyLogin = ({ history }) => {

    const { ispassdoorman } = useSelector(state => state.login);
    const { user } = useSelector(state => state.login);
    const { cloudAppVersion } = useSelector(state => state.login);
    const staticFileVersion = productionVersion;

    //檢測登入狀態
    useEffect(() => {
        if (process.env.NODE_ENV === "development") {
            return;
        }

        if (auth) {
            onAuthStateChanged(auth, (user) => {
                if (user) {
                    // User is signed in, see docs for a list of available properties
                    // https://firebase.google.com/docs/reference/js/firebase.User
                } else {
                    // User is signed out
                    console.log("User is signed out");
                    toast.error("目前為登出狀態");
                    history.replace(paths.DoormanPageUrl);
                }
            });
        }
    }, [])

    //檢測連線狀態
    useEffect(() => {
        const connectedRef = databaseRef(db, ".info/connected");
        onValue(connectedRef, (snap) => {
            if (snap.val() === true) {
                console.log("connected");
                toast.success("connected");
            } else {
                console.log("not connected");
                toast.error("not connected");
            }
        });
    }, [])


    //檢查當前使用版本 (local) 跟最新版本 (firebase) 是否有差異，若有差異則強迫使用者重新載入
    useEffect(() => {

        if (cloudAppVersion && !isStringEqual(staticFileVersion, cloudAppVersion)) {

            // 開發測試階段跳過驗證
            if (process.env.NODE_ENV === "development") {
                console.log(`localAppVersion: ${staticFileVersion}`)
                console.log(`cloudAppVersion: ${cloudAppVersion}`)
            }

            const isConfirm = window.confirm(`版本更新，請刷新網頁並重新登入。\n 若提示重複出現，請清除瀏覽器快取 (cache)，並再試一次。 \n 當前版本: ${staticFileVersion} \n 更新版本: ${cloudAppVersion}`);

            //強迫重新登入
            if (isConfirm) {
                window.location.href = "about:blank";
            } else {
                window.location.href = "about:blank";
            }

            window.location.href = "about:blank";
        }

    }, [cloudAppVersion])

    //讀取最新版本 (firebase)
    useEffect(() => {
        const maxMessageNumber = 5;

        if (appVersionRef) {
            const limitAppVersionRef = query(appVersionRef, limitToLast(5));

            onValue(limitAppVersionRef, (snapshot) => {
                const data = snapshot.val();
                if (data !== null && data !== undefined) {
                    // 最多顯示 5 則版本資訊
                    const sliceData = _.slice(_.values(data), -maxMessageNumber);

                    // 最新一則 Po 文的使用者
                    const currentVersion = _.slice(_.values(sliceData), -1)[0];

                    reduxDispatch(loginTypes.SET_APP_VERSION, currentVersion);
                }
            });
        }
    }, [])

    useEffect(() => {
        if (staticFileVersion && user && user === linda) {
            sendTelegramMessage(`https://klk.coffee/version${staticFileVersion}`);
        }
    }, [staticFileVersion])

    return (
        <>
            <ToastContainer
                theme='dark'
                position="bottom-center"
                autoClose={1000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />
        </>
    )
}

const isStringEqual = (text1, text2) => {
    return new String(text1).valueOf() == new String(text2).valueOf();
}

export default withRouter(VerifyLogin);