/*eslint-disable*/
import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import blogPostPageStyle from "assets/jss/material-kit-pro-react/views/blogPostPageStyle.js";
import componentsStyle from "assets/jss/material-kit-pro-react/views/componentsStyle";
import ChatPage from "./ChatPage";

const useStyles = makeStyles(blogPostPageStyle);
const useComponentStyles = makeStyles(componentsStyle);

const ChatLayoutPage = () => {
    React.useEffect(() => {
        window.scrollTo(0, 0);
        document.body.scrollTop = 0;
    });

    const classes = useStyles();
    const compClasses = useComponentStyles();

    return (
        <div>
            <Header
                brand="Klk Coffee"
                links={<HeaderLinks dropdownHoverColor="dark" />}
                fixed
                color="transparent"
                changeColorOnScroll={{
                    height: 400,
                    color: "dark"
                }}
            />
            <Parallax image={require("assets/img/bg17_min.jpg")}
                // filter="dark"
                className={classes.parallax}
            >
                <div className={classes.container}>
                    <GridContainer justify="center">
                        <GridItem>
                            <div className={compClasses.brand}>
                                <h1>
                                    KLK Coffee{" "}
                                </h1>
                                <h3 className={compClasses.title}>CHAT</h3>
                            </div>
                        </GridItem>
                    </GridContainer>
                </div>
            </Parallax>
            <div className={classes.main} style={chatContainerStyle}>
                <div className={classes.container}>
                    <ChatPage />
                </div>
            </div>
        </div>
    );
}

const chatContainerStyle = {
    backgroundColor: 'gainsboro',
}


export default ChatLayoutPage;