const getCookieExpire = (minute) => {
    let d = new Date();
    d.setTime(d.getTime() + minute * 60 * 1000); // in milliseconds
    return d;
}

const defaultCookieExpireMinutes = 120;

const cookieOptions = { expires: getCookieExpire(defaultCookieExpireMinutes), sameSite: true };

export default cookieOptions;